import listCell from '@/components/mix-list-cell.vue';
import MescrollUni from "@/components/mescroll-uni/mescroll-uni.vue"; // 拷贝到components的,则引入.vue文件 (注意.vue后缀不能省)
import {
	mapState
} from 'vuex';
let startY = 0,
	moveY = 0,
	pageAtTop = true;
export default {
	components: {
		listCell,
		MescrollUni,
	},
	data() {
		return {
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			userInformation: {
				'headimg': '',
				'nickname': ''
			},
			// 下拉刷新的常用配置
			downOption: {
				use: true, // 是否启用下拉刷新; 默认true
				auto: true, // 是否在初始化完毕之后自动执行下拉刷新的回调; 默认true
			},
			// 上拉加载的常用配置
			upOption: {
				use: false, // 是否启用上拉加载; 默认true
				auto: false, // 是否在初始化完毕之后自动执行上拉加载的回调; 默认true
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 3 // 每页数据的数量,默认10
				},
				noMoreSize: 5, // 配置列表的总数量要大于等于5条才显示'-- END --'的提示
				empty: {
					tip: '暂无相关数据...'
				}
			}
		}
	},
	mounted() {
		this.getPageData();
		this.getAd();
	},




















	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		getAd() {
			let _this = this;
			// 在页面中定义插屏广告
			let interstitialAd = null
		
			// 在页面onLoad回调事件中创建插屏广告实例
			if (wx.createInterstitialAd) {
				_this.screenAd = wx.createInterstitialAd({
					adUnitId: 'adunit-d7c1f207fcff3ab6'
				})
				_this.screenAd.onLoad(() => {})
				_this.screenAd.onError((err) => {})
				// _this.screenAd.onClose(() => {})
			}
			// 在适合的场景显示插屏广告
			if (_this.screenAd) {
				setTimeout(function(){
					_this.screenAd.show().catch((err) => {
						console.error(err)
					})
				}, _this.getRandom(15000, 30000))
			}
		},
		getRandom(start, end, fixed=0) {
		            let differ = end - start
		            let random = Math.random()
		            return (start + differ * random).toFixed(fixed)
		},
		couponsList(){
			uni.navigateTo({
				url:'/pages/coupons/list'
			})
		},
		getCoupons(){
			uni.navigateTo({
				url:'/pages/coupons/get'
			})
		},
		//分享
		shareTo() {
			uni.showToast({
				'title':'请点击上方工具栏分享转发',
				'icon': 'none'
			})
		},
		tip() {
			this.$api.msg('暂未开通');
		},
		/*下拉刷新的回调, 有三种处理方式: */
		downCallback(mescroll) {
			// 第1种: 请求具体接口
			this.getPageData()
			mescroll.endSuccess()
		},
		editInfo() {
			uni.navigateTo({
				// url: '/pages/user/personal'
			})
		},
		getPageData: function() {
			let _this = this;
			this.$http.post('/api/user/index').then(res => {
				var data = res.data.data
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					if (data['headimg'] != '' || data['nickname'] != '') {
						_this.userInformation = data;
					} else {
						// _this.wxGetUserInfo();
						uni.navigateTo({
							url: '/pages/user/auth'
						})
					}
				}
			}).catch(err => {
				console.log(err);
			})
		},
		wxGetUserInfo: function() {
			let that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							console.log(infoRes);
							that.userInformation.headimg = infoRes.userInfo.avatarUrl;
							that.userInformation.nickname = infoRes.userInfo.nickName;
							that.saveUserInfo(infoRes.userInfo);
						}
					});
				}
			});
		},
		saveUserInfo: function(userInfo) {
			this.$http.post('/api/user/saveBase', userInfo).then(res => {
				var data = res.data.data
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					console.log(res)
				}
			}).catch(err => {
				console.log(err);
			})
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			if (!this.userInfo) {
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		},

		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		}
	}
}
